import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import zp from '../views/zp.vue'
import lx from '../views/lx.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
},{path:'/zp',name:'zp',component:zp},
  {path:'/lx',name:'lx',component:lx}
]

const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
